* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.search-form {
  background: #96c527;
  justify-content: center;
  align-items: center;
  padding: 20px;
  display: flex;
}

#search-box {
  font: inherit;
  max-width: 320px;
  border-radius: 20px;
  margin-bottom: 16px;
  padding: 8px;
}

.input {
  width: 50%;
  height: 48px;
  background: #fff;
  border: 1px solid rgba(221, 13, 65, .12);
  border-radius: 30px;
  margin-top: 2px;
  padding-left: 12px;
  font-size: 16px;
  line-height: 1.25;
  position: relative;
  box-shadow: inset 0 2px 4px rgba(0, 0, 0, .15);
}

.submit {
  background-color: fff;
  cursor: pointer;
  border: 1px solid rgba(18, 18, 18, .12);
  border-radius: 100px;
  justify-content: center;
  margin-left: 50px;
  padding: 10px 40px;
  font-family: -apple-system;
  font-size: 21px;
  position: relative;
  overflow: hidden;
  box-shadow: inset 0 2px 4px rgba(0, 0, 0, .15);
}

.submit:hover, .submit:focus {
  background-color: #7fffd4;
}

.list {
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 20px;
  list-style: none;
  display: flex;
}

.info-item, .info {
  justify-content: center;
  align-items: center;
  display: flex;
}

.info-item {
  padding: 5px;
  font-family: -apple-system;
  font-size: 10px;
}

.image {
  justify-content: center;
  align-items: center;
  margin: auto;
  display: flex;
}

.load-more {
  cursor: pointer;
  background: #7ee49c;
  border: 1px solid #000;
  border-radius: 100px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  padding: 10px 40px;
  font-family: -apple-system;
  font-size: 21px;
  display: none;
  position: relative;
  overflow: hidden;
  box-shadow: inset 0 2px 4px rgba(64, 230, 22, .59);
